import React, { useEffect, useState } from "react";
import "./TestDetails.scss";

import { Outlet, useParams } from "react-router-dom";
import { getUserList } from "../../Redux/UserList/UserList";
import { getTestDetails } from "../../Redux/TestDetailReducer/TestDetailSlice";
import { useDispatch, useSelector } from "react-redux";
import { NavLink, useNavigate } from "react-router-dom";

// icons
import { MdPriorityHigh } from "react-icons/md";
import { BsArrowLeftShort } from "react-icons/bs";
import { BiEdit } from "react-icons/bi";
/* import { CiUser } from "react-icons/ci"; */

// utils
import Button from "component/common/Button/Button";
import axiosWithAuth from "../../utils/axiosClienttok";
import { getActivityList } from "../../Redux/activityReducer/ActivityReducer";
import Dialog from "component/common/Dialog/Dialog";

function TestDetails() {
  // states
  const [enableEdit, setEnableEdit] = useState(true);

  const [status, setStatus] = useState("");
  const [automationStatus, setAutomationStatus] = useState("");
  const [Priorities, setPriority] = useState("");

  const [reason, setReason] = useState("");
  const [reasonPop, SetReasonPop] = useState(false);

  const params = useParams();
  console.log("params", params);
  console.log("params");
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const match = params.id;
  console.log(match);

  const testDetail = useSelector((state) => state.TestDetailReducer.testDetail);
  const [name, setName] = useState(testDetail?.name || "");
  const usersList = useSelector((state) => state.UserList.list);

  const [assignee, setAssignee] = useState("");
  //  run on initial render and get the test detail and userList  from redux
  useEffect(() => {
    dispatch(getTestDetails(match));
    dispatch(getUserList());
  }, []);

  async function updateTestInfo() {
    var statusP = testDetail?.status;
    var automationStatusP = testDetail?.automation_status;
    var priorityP = testDetail?.priority;
    var nameP = testDetail?.name;
    var changed = [];

    if (status !== "") {
      statusP = status;
      changed.push("Status");
    }
    if (automationStatus !== "") {
      automationStatusP = automationStatus;
      changed.push("Automation Status");
    }
    if (Priorities !== "") {
      priorityP = Priorities;
      changed.push("Priority");
    }
    if (name !== testDetail?.name) {
      nameP = name;
      changed.push("name");
    }
    try {
      if (changed.length > 0) {
        const response = await axiosWithAuth.patch(`core/testcase/${match}`, {
          name: nameP,
          status: statusP,
          automation_status: automationStatusP,
          priority: priorityP,
          history_change_reason: reason
        });
        console.log(changed);
        console.log(response);
      }
      if (assignee !== testDetail?.assigned) {
        const response2 = await axiosWithAuth.patch(`core/testcase/${match}`, {
          assigned: assignee,
          history_change_reason: reason
        });
        console.log("response2", response2);
      }
    } catch (error) {
      console.log(error);
    } finally {
      dispatch(getTestDetails(match));
      dispatch(getActivityList(match));
      setEnableEdit(true);
      setAutomationStatus("");
      setPriority("");
      setStatus("");
      setReason("");
      setAssignee("");
      setName("");
      SetReasonPop(false);
    }
  }
  const handleStatusChange = (e) => {
    setStatus(e.target.value);
  };

  const handleAutomationStatusChange = (e) => {
    setAutomationStatus(e.target.value);
  };

  const handlePriorityChange = (e) => {
    console.log("priority", e.target.value);
    setPriority(e.target.value);
  };

  const handleAssigneeChange = (e) => {
    setAssignee(e.target.value);
  };

  const formatClassName = (className) => {
    switch (className) {
      case "class_1":
        return "Class 1";
      case "class_2":
        return "Class 2";
      case "class_3":
        return "Class 3";
      default:
        return className;
    }
  };

  const formatAutomationStatus = (atStatus) => {
    switch (atStatus) {
      case "not-automatable":
        return "Not-Automatable";
      case "automatable":
        return "Automatable";
      case "in-development":
        return "In-Development";
      case "review":
        return "Review";
      case "ready":
        return "Ready";
      default:
        return atStatus;
    }
  };

  const formatStatus = (atStatus) => {
    switch (atStatus) {
      case "todo":
        return "To-Do";
      case "ongoing":
        return "On-Going";
      case "completed":
        return "Completed";
      default:
        return atStatus;
    }
  };
  const handleChangeName = (e) => {
    setName(e.target.value);
  };
  function handleCloseReasonPopUp() {
    SetReasonPop(false);
  }
  useEffect(() => {
    if (testDetail?.name) {
      setName(testDetail.name);
    }
  }, [testDetail]);
  useEffect(() => {
    if (testDetail?.assigned) {
      setAssignee(testDetail.assigned);
    }
  }, [testDetail]);

  return (
    <div className="testDetail_page">
      {reasonPop && (
        <Dialog
          isOpen={reasonPop}
          title="Add Reason For Change"
          onClose={handleCloseReasonPopUp}
          confirm={{ text: "Confirm", onConfirm: updateTestInfo }}
        >
          <div className="textarea">
            <textarea
              spellCheck={false}
              autoComplete="off"
              type="text"
              id="inputField"
              name="inputField"
              value={reason}
              onChange={(e) => setReason(e.target.value)}
            ></textarea>
          </div>
        </Dialog>
      )}

      <div className="test_detail">
        <div className="inner_testdetail">
          <div className="testcase-info-box">
            <div className="test_case_name-and-info">
              {enableEdit === false && (
                <div
                  className="save-button"
                  style={{
                    position: "absolute",
                    right: "10px",
                    bottom: "10px",
                    fontSize: "0.8vw",
                    cursor: "pointer"
                  }}
                >
                  <Button size="small" type="primary" onClick={() => SetReasonPop(true)} iconPosition="left">
                    Save
                  </Button>
                </div>
              )}

              <div className="testcase-name-status">
                <div className="navigate-edit">
                  <p
                    onClick={() =>
                      params.type === "performance" || params.type === "smoke" || params.type === "soak"
                        ? navigate(`/test-case/${params.type}`)
                        : navigate(`/test-plans/${params.type}`)
                    }
                  >
                    {/*   <p onClick={() => navigate(-1)}> */}
                    <BsArrowLeftShort style={{ fontSize: "1rem" }} /> Back
                  </p>
                  <p onClick={() => setEnableEdit(!enableEdit)}>
                    <BiEdit style={{ fontSize: "1vw" }} />
                  </p>
                </div>
                <div className="name-id">
                  <h2>{testDetail?.id}</h2>
                  {enableEdit ? (
                    <h2>{testDetail?.name}</h2>
                  ) : (
                    <input
                      type="text"
                      style={{ width: "100%", padding: "5px" }}
                      value={name}
                      onChange={handleChangeName}
                    />
                  )}
                </div>

                <div className="st">
                  <p style={{ width: "40%", color: "#5d7290" }}>Status</p>
                  {enableEdit ? (
                    <p
                      style={{
                        backgroundColor: "#ebf0f5",
                        fontSize: "0.9vw",
                        padding: "1px 6px",
                        border: "1.5px solid #d7dee7",
                        borderRadius: "4px",
                        color: "#455365"
                      }}
                    >
                      {formatStatus(testDetail?.status)}
                    </p>
                  ) : (
                    <div className="form-group-drop">
                      <select
                        id="status"
                        name="status"
                        value={status}
                        onChange={handleStatusChange}
                        style={{
                          outline: "none",
                          border: "1.5px solid #d7dee7",
                          padding: "1px 5px",
                          borderRadius: "4px",
                          transition: "border-color 0.3s, box-shadow 0.3s",
                          fontSize: "0.9vw",
                          color: "#525b6c",
                          fontWeight: "500",
                          backgroundColor: "#ebf0f5"
                        }}
                        onFocus={(e) => {
                          e.target.style.border = "1.5px solid #d7dee7";
                          e.target.style.boxShadow = "0 0 2px #d7dee7";
                        }}
                        onBlur={(e) => {
                          e.target.style.border = "1.5px solid #d7dee7";
                          e.target.style.boxShadow = "none";
                        }}
                      >
                        <option value="" disabled>
                          Status
                        </option>
                        <option value="todo">To Do</option>
                        <option value="ongoing">On Going</option>
                        <option value="completed">Completed</option>
                      </select>
                    </div>
                  )}
                </div>

                <div className="st">
                  <p style={{ width: "40%", color: "#5d7290" }}>Automation Status</p>
                  {enableEdit ? (
                    <p
                      style={{
                        backgroundColor: "#ebf0f5",
                        fontSize: "0.9vw",
                        padding: "1px 6px",
                        border: "1.5px solid #d7dee7",
                        borderRadius: "4px",
                        color: "#455365"
                      }}
                    >
                      {formatAutomationStatus(testDetail?.automation_status)}
                    </p>
                  ) : (
                    <div className="form-group-drop">
                      {/* <label htmlFor="priority" style={{ marginRight: "10px" }}>
                  TestCase Type:
                </label> */}
                      <select
                        id="Automation"
                        name="Automation"
                        value={automationStatus}
                        onChange={handleAutomationStatusChange}
                        style={{
                          outline: "none",
                          border: "1.5px solid #d7dee7",
                          padding: "1px 5px",
                          borderRadius: "4px",
                          transition: "border-color 0.3s, box-shadow 0.3s",
                          fontSize: "0.9vw",
                          color: "#525b6c",
                          fontWeight: "500",
                          backgroundColor: "#ebf0f5"
                        }}
                        onFocus={(e) => {
                          e.target.style.border = "1.5px solid #d7dee7";
                          e.target.style.boxShadow = "0 0 2px #d7dee7";
                        }}
                        onBlur={(e) => {
                          e.target.style.border = "1.5px solid #d7dee7";
                          e.target.style.boxShadow = "none";
                        }}
                      >
                        <option value="" disabled>
                          Automation Status
                        </option>
                        <option value="not-automatable">Not-Automatable</option>
                        <option value="automatable">Automatable</option>
                        <option value="in-development">In-Development</option>
                        <option value="review">Review</option>
                        <option value="ready">Ready</option>
                      </select>
                    </div>
                  )}
                </div>

                <div className="st">
                  <p style={{ width: "40%", color: "#5d7290" }}>Priority</p>
                  {enableEdit ? (
                    <p
                      style={{
                        backgroundColor: "#ebf0f5",
                        fontSize: "0.9vw",
                        padding: "2px 6px",
                        border: "1.5px solid #d7dee7",
                        borderRadius: "4px",
                        color: "#455365",
                        display: "flex",
                        alignItems: "center",
                        gap: "4px"
                      }}
                    >
                      {" "}
                      <MdPriorityHigh
                        style={{
                          color:
                            testDetail?.priority === "class_1"
                              ? "#FF8080"
                              : testDetail?.priority === "class_2"
                                ? "#FABC3F"
                                : testDetail?.priority === "class_3"
                                  ? "green"
                                  : "black",

                          fontWeight: "900",
                          fontSize: "1rem"
                        }}
                      />{" "}
                      <span style={{ margin: "0px" }}>{formatClassName(testDetail?.priority)}</span>
                    </p>
                  ) : (
                    <div className="form-group-drop">
                      <select
                        id="priority"
                        name="priority"
                        value={Priorities}
                        onChange={handlePriorityChange}
                        style={{
                          outline: "none",
                          border: "1.5px solid #d7dee7",
                          padding: "1px 5px",
                          borderRadius: "4px",
                          transition: "border-color 0.3s, box-shadow 0.3s",
                          fontSize: "0.9vw",
                          color: "#525b6c",
                          fontWeight: "500",
                          backgroundColor: "#ebf0f5"
                        }}
                        onFocus={(e) => {
                          e.target.style.border = "1.5px solid #d7dee7";
                          e.target.style.boxShadow = "0 0 2px #d7dee7";
                        }}
                        onBlur={(e) => {
                          e.target.style.border = "1.5px solid #d7dee7";
                          e.target.style.boxShadow = "none";
                        }}
                      >
                        <option value="" disabled>
                          Priority
                        </option>
                        <option value="class_1">Class 1</option>
                        <option value="class_2">Class 2</option>
                        <option value="class_3">Class 3</option>
                      </select>
                    </div>
                  )}
                </div>

                <div className="st">
                  <p style={{ width: "40%", color: "#5d7290" }}>TestCase Type</p>
                  <p
                    style={{
                      backgroundColor: "#ebf0f5",
                      fontSize: "0.9vw",
                      padding: "1px 6px",
                      border: "1.5px solid #d7dee7",
                      borderRadius: "4px",
                      color: "#455365"
                    }}
                  >
                    {testDetail?.testcase_type.charAt(0).toUpperCase() + testDetail?.testcase_type.slice(1)}
                  </p>
                </div>
              </div>

              <div className="description">
                {/* <p className="name">Description</p>
              <p className="value">
                The purpose of this test case is to rewind VOD content to the
                beginning for PL natco. Precondition * User is logged in. * VOD
                content available to watch.
              </p> */}

                <div className="people">
                  <p className="name">People</p>

                  <div className="role-section">
                    <div className="stp">
                      <p style={{ width: "40%", color: "#5d7290" }}>Assignee</p>
                      {enableEdit ? (
                        <p
                          style={{
                            backgroundColor: "#ebf0f5",
                            fontSize: "0.9vw",
                            padding: "1px 6px",
                            border: "1.5px solid #d7dee7",
                            borderRadius: "4px",
                            color: "#455365"
                          }}
                        >
                          {testDetail?.assigned || "-"}
                        </p>
                      ) : (
                        <div className="form-group-drop">
                          <select
                            id="assignee"
                            name="assignee"
                            value={assignee}
                            onChange={handleAssigneeChange}
                            style={{
                              outline: "none",
                              border: "1.5px solid #d7dee7",
                              padding: "1px 5px",
                              borderRadius: "4px",
                              fontSize: "0.9vw",
                              color: "#525b6c",
                              fontWeight: "500",
                              backgroundColor: "#ebf0f5"
                            }}
                          >
                            <option value="" disabled>
                              Select Assignee
                            </option>
                            {usersList.map((user) => (
                              <option key={user.email} value={user.email}>
                                {user.email}
                              </option>
                            ))}
                          </select>
                        </div>
                      )}
                    </div>

                    {/*         <div className="stp">
                      <p
                        style={{
                          width: "40%",
                          color: "#6c809b",
                          fontSize: "0.9vw"
                        }}
                      >
                        Asignee
                      </p>
                      <p
                        style={{
                          width: "60%",
                          color: "#425062",
                          fontSize: "0.9vw"
                        }}
                      >
                        {testDetail?.assigned ? (
                          <>
                            <CiUser /> <span style={{ paddingLeft: "5px" }}>{testDetail?.assigned}</span>
                          </>
                        ) : (
                          "-"
                        )}
                      </p>
                    </div>
 */}
                    <div className="stp">
                      <p
                        style={{
                          width: "40%",
                          color: "#6c809b",
                          fontSize: "0.9vw"
                        }}
                      >
                        Reporter
                      </p>
                      <p
                        style={{
                          width: "60%",
                          color: "#425062",
                          fontSize: "0.9vw"
                        }}
                      >
                        {testDetail?.created_by ? (
                          <>
                            {/*       <CiUser /> */}
                            <span>{testDetail?.created_by}</span>
                          </>
                        ) : (
                          "-"
                        )}
                      </p>
                    </div>
                  </div>
                </div>
                <div className="people">
                  <p className="name">Dates</p>

                  <div className="role-section">
                    <div className="stp">
                      <p
                        style={{
                          width: "40%",
                          color: "#6c809b",
                          fontSize: "0.9vw"
                        }}
                      >
                        Created
                      </p>
                      <p
                        style={{
                          width: "60%",
                          color: "#425062",
                          fontSize: "0.9vw"
                        }}
                      >
                        {testDetail?.created ? testDetail?.created : null}
                      </p>
                    </div>

                    <div className="stp">
                      <p
                        style={{
                          width: "40%",
                          color: "#6c809b",
                          fontSize: "0.9vw"
                        }}
                      >
                        Modified
                      </p>
                      <p
                        style={{
                          width: "60%",
                          color: "#425062",
                          fontSize: "0.9vw"
                        }}
                      >
                        {testDetail?.modified ? testDetail?.modified : null}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="tab-and-outlet">
            <div className="tab_for_test_case">
              <div className="details tab">
                <p>
                  <NavLink className="links" activeClassName="active" to="details">
                    Details
                  </NavLink>
                </p>
              </div>
              <div className="description_steps tab">
                <p>
                  <NavLink className="links" activeClassName="active" to="description-step">
                    Test steps
                  </NavLink>
                </p>
              </div>
              {testDetail?.automation_status !== "not-automatable" && (
                <div className="automation-script tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="automation-script">
                      Automation Script
                    </NavLink>
                  </p>
                </div>
              )}
              <div className="test_group tab">
                <p>
                  <NavLink className="links" activeClassName="active" to="test-group">
                    Test group
                  </NavLink>
                </p>
              </div>
              <div className="Last_50_run tab">
                <p>
                  <NavLink className="links" activeClassName="active" to="last-run">
                    Last 50 run
                  </NavLink>
                </p>
              </div>
              <div className="linked_defects tab">
                <p>
                  <NavLink className="links" activeClassName="active" to="defects">
                    Linked defects
                  </NavLink>
                </p>
              </div>
              {/* {testDetail?.automation_status !== "not-automatable" && (
                <div className="issue tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="script-issue">
                      Script Issue
                    </NavLink>
                  </p>
                </div>
              )} */}
              {testDetail?.automation_status !== "not-automatable" && (
                <div className="details tab">
                  <p>
                    <NavLink className="links" activeClassName="active" to="natco">
                      Natco
                    </NavLink>
                  </p>
                </div>
              )}
              <div className="activities tab">
                <p>
                  <NavLink className="links" activeClassName="active" to="activity">
                    Activity
                  </NavLink>
                </p>
              </div>
            </div>

            <div className="display">
              <Outlet />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TestDetails;
